import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { MainLoading } from "../../Components";

const Splash = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const timer = setTimeout(() => {
      navigate("/auth", { replace: true });
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [navigate]);

  return <MainLoading />;
};

export default Splash;
